import { Component } from '@angular/core';
import { IpartSettingsService } from './ipart-settings.service';
import { ContentItemData } from './models/content-item-data';
import { HttpClient, HttpParams, HttpBackend } from '@angular/common/http';
import { AppConstantService } from '@advsol/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = ' Hussein Calling Current User!';
  data: MySettings; // this is JSON data
  currentUser: CurrentUser;
  loggedInPartyId = "";
  private httpClient: HttpClient;

  constructor(private settings: IpartSettingsService, private http: HttpClient, httpClient: HttpBackend, private context: AppConstantService) {
    this.loggedInPartyId = context.loggedInPartyId;
    this.httpClient = new HttpClient(httpClient);
  }
  // Using commonService
  ngOnInit(): void {
    this.settings.GetSettings().subscribe((data: ContentItemData) => {
      this.data = data.Data as MySettings;
    });
    this.getLoggedInUserInfo();
  }

  // Without CommonService
  getDataFromBackend(): void {
    // const ck = (document.querySelector("#x-contentKey") as HTMLInputElement).value;
    // const cik = (document.querySelector("#x-contentItemKey") as HTMLInputElement).value;
    // const params = new HttpParams()
    // .set('contentKey', ck)
    // .set('contentItemKey',cik);


    const domain = window.location.href;
    const token = this.context.authToken;
    const url = "https://openwater-os.secure-platform.com/ipartfactory/test";
    // const url = "api/party";
    const params = new HttpParams()
      .set('domain', domain)
      .set('token', token)
      .set('partyId', this.loggedInPartyId);


    this.httpClient.get(url, { params: params }).subscribe((data) => {

      console.log(data);
    });
  }

  getLoggedInUserInfo(): void {
    const url = `api/party/${this.loggedInPartyId}`;
    console.log(url);
    this.http.get(url, { params: {} }).subscribe((data) => {
      var currentUserJson = data as CurrentUser;
      this.currentUser.id = currentUserJson.id;
      this.currentUser.name = currentUserJson.name;
      this.currentUser.partyId = currentUserJson.partyId;
      console.log(data);
    });
  }
}




interface MySettings {
  myStringSetting?: string;
  myBoolSetting?: string;
  mySelectSetting?: string;
}

interface CurrentUser {
  id: string,
  name: string,
  partyId: string
}



